<template>
	<view class="form">
		<u-form :model="item" label-width="140" :label-style="labelStyle">
			<u-form-item :label="item.name + ':'" prop="name">
				<view class="input-box" v-if="item.type != 'radio' && item.type !== '@'">
					<u-input v-model="item.value" :value="item.value" :type="item.type !== '@' ? item.type : ''" :placeholder="placeholderText + item.name" :style="'width:' + handleInputStyle(item)"/>
				</view>
				<view v-if="item.name == '户籍地址'" style="display: flex;align-items: center;">
					<u-switch slot="right" v-model="switchVal" size="40"></u-switch>
					<text>同现地址</text>
				</view>
				<view v-if="item.name == '现住址' || item.name == '户籍地址'" style="display: flex;flex-direction: column;flex-shrink: 0;">
					<u-input v-model="item.value" disabled :placeholder="placeholderText + item.name"/>
					<u-input v-model="item.values" :placeholder="item.placeholders"/>
				</view>
				<view class="u-radio-group-box" v-if="item.type == 'radio'">
					<u-radio-group v-model="radio">
						<u-radio v-for="(jtem, jndex) in item.children" :key="jndex" :name="jtem.name" :disabled="jtem.disabled">
							{{ jtem.name }}
						</u-radio>
					</u-radio-group>
				</view>
			</u-form-item>
		</u-form>
	</view>
</template>

<script>
	export default {
		props: {
			placeholderText:{
				type: String,
				default: '妻子'
			},
			item: {
				type: Object,
				default: () => {
					return {}
				}
			}
		},
		data() {
			return {
				labelStyle: {
					lineHeight: '40rpx'
				},
				switchVal: false
			}
		},
		computed: {
			handleInputStyle(){
				return function(item){
					return item.type == 'select' ? '100%;' : ''
				}
			}
		},
		mounted() {
			
		}
	}
</script>

<style scoped lang="scss">
	.form {
		padding: 0 40rpx;

		.input-box {
			height: 40rpx;
			display: flex;
			align-items: center;
			flex-shrink: 0;
		}

		.u-radio-group-box {
			height: 40rpx;
			display: flex;
			align-items: center;
			flex-shrink: 0;
		}
	}
</style>
